.two-columns{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 5px;
}

#plan-section{
    display : inline-block;
    padding : 10px 0px;
    width : 400px;
    grid-column: 1 / span 1;
    justify-self: end;
}

#meal-section{
    display : inline-block;
    padding : 10px 0px;
    width : 400px;
    grid-column: 2 / span 1;
}

#plan-section form {
    width : 400px;
}

.check-tag{
    display : inline;
    width : auto;
    margin-right : 2px;
}

.tag{
    display : inline-block;
    width : auto;
    font-size: 14px;
    margin-right : 6px;
    background-color: rgb(232, 228, 187);
    border-radius: 10%;
    padding : 3px;
    margin : 5px 5px;
    height : 15px;
}

.tag label, .tag input{
    line-height: 0;
    height : 10px;
    margin : 0 0;
}

.diet-options, .health-options{
    text-align: left;
    padding : 10px 10px;
}

section.recipe{
    display : block;
    position : absolute;
    background-color: rgba(255, 255, 255, 0.929);
    width : 800px;
    /* height : 600px; */
    z-index : 100;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    border : solid;
}

.recipe-header{
    background-color: rgba(0, 85, 255, 0.43);
}

.delete-meal-plan{

}

.confirm-delete-plan{
    background-color: rgba(255, 114, 114, 0.43);
    position : absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.confirm-delete-plan button{
    display : block;
    margin : 20px auto;
}

.inner-div{
    background-color:blanchedalmond;
    margin : 30px;
    filter: drop-shadow(10px 6px 6px #21216a);
    padding : 10px;
    border-radius: 10px;
}

.cancel-delete{
    background-color: chartreuse;
}

.confirm-delete{
    background-color: rgb(239, 23, 23);
}